import BButton from 'base/b-button/b-button';
import {split} from 'common/js/array';
export default {
  data(){
    return {
      list:[],
      showFlag: false
    }
  },
  components: {
    BButton
  },
  props: {
    info: {
      type: String,
      default: ''
    }
  },
  created(){
    this.list=split(this.info);
  },
  methods: {
    show(){
      this.showFlag = true;
    },
    hide(){
      this.showFlag = false;
    }
  }
}



